//import liraries
import React, {useEffect, useState} from 'react';
import {View, ScrollView, TouchableOpacity} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../../../constants/Size';
import {
  IconDownload,
  IconTrash,
  IconUpload,
  IconView,
} from '../../../../../../../assets/images';
import TableCM from '../../../../../../components/Table';
import {IInfoFile, IInfoFileRelative, IInfoForm} from './interface';
import {CommonUtils, DateUtils} from '../../../../../../../utils';
import TextCM from '../../../../../../components/Text';
import PagingCM from '../../../../../../components/Paging';
import {useNavigation} from '@react-navigation/native';
import {NavigationName} from '../../../../../../../constants';
import Toast from 'react-native-toast-message';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {Status} from '../../../../../../../model/enum/common.enum';
import useFileDownloader from '../../../../../../../hooks/useFileDownloader';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../../../../hooks/apiHooks/useReviewTicketServices';
type Props = {};
// create a component
const Document = ({}: Props) => {
  const pageSize = 3;
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const [currentPage, setCurrentPage] = useState(1);
  const [totalItems, setTotalItems] = useState<number>(0);
  const [dataInfoFile, setDataInfoFile] = useState<any[]>([]);
  const [dataForm, setDataForm] = useState<any[]>([]);
  const [dataRelative, setDataRelative] = useState<any[]>([]);

  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {onDownloadStart} = useFileDownloader(
    'a.jpg',
    'https://econ-eform-demo.s3-hfx03.fptcloud.com/6727/20240624/1719228579748_352363448_108886518898709_3449798472827587508_n.jpg',
  );
  const {accessToken} = useAppSelector(state => state.common);
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    getListInfoFile();
    getListForm();
  }, []);

  useEffect(() => {
    getListRelativeFile(currentPage);
  }, [currentPage]);

  const getListInfoFile = async () => {
    try {
      const body = {
        ticketId: dataTicketRedux?.id,
        fileId: null,
      };

      const data = await ReviewTicketServices.getInfoFile(body, accessToken);
      if (data) {
        convertDataToTable(data);
      }
    } catch (error) {}
  };

  const getListForm = async () => {
    try {
      const dataStatusParty = await ReviewTicketServices.getStatusParty(
        dataTicketRedux?.id,
        accessToken,
      );
      if (!dataStatusParty) return;
      const statusParty = dataStatusParty?.party?.recipients;
      const listDataModelerForm =
        await ReviewTicketServices.getListFormModelerById(
          dataTicketRedux?.modelerId,
          accessToken,
        );
      if (listDataModelerForm && listDataModelerForm.data.length > 0) {
        Promise.all(
          listDataModelerForm.data.map((data: any) =>
            ReviewTicketServices.getFormDetailById(data.formId, accessToken),
          ),
        ).then((values: IInfoForm[]) => {
          convertDataFromToTable(values, listDataModelerForm.data, statusParty);
        });
      }
    } catch (error) {
      console.log('error', error);
    }
  };

  const getListRelativeFile = async (page: number) => {
    try {
      const params = {
        size: pageSize,
        page: page - 1,
        sort: 'createdDate,desc',
        ticketId: dataTicketRedux?.id,
      };

      const data = await ReviewTicketServices.getListFileRelative(
        params,
        accessToken,
      );
      if (data) {
        if (data?.data?.length === 0) {
          if (page > 1) {
            setCurrentPage(page - 1);
          } else {
            setDataRelative([]);
            setTotalItems(0);
          }
          return;
        }
        setTotalItems(data?.totalRecords);
        convertDataFileToTable(data?.data);
      }
    } catch (error) {}
  };

  const convertDataToTable = (data: IInfoFile[]) => {
    const newData = data.map((infoFile: IInfoFile, index: number) => ({
      STT: String(index + 1),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {infoFile?.keyName}
        </TextCM>
      ),
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile.documentSize),
      type: infoFile?.keyName?.split('.').slice(-1)[0],
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(true, infoFile),
    }));
    setDataInfoFile(newData);
  };

  const convertDataFromToTable = (
    data: IInfoForm[],
    dataModelerForm: any[],
    statusParty: any[],
  ) => {
    const newData = data.map((infoForm: IInfoForm, index: number) => ({
      STT: String(index + 1),
      keyName: (
        <TouchableOpacity
          onPress={() =>
            navigation.navigate(NavigationName.ViewFormScreen, {
              isOnlyView: true,
              readOnly: true,
              versionId: dataModelerForm?.find(
                data => data.formId === infoForm.formId,
              )?.versionId,
              viewFromDoc: true,
            })
          }>
          <TextCM style={styles.txtText} numberOfLines={1}>
            {infoForm?.formName}
          </TextCM>
        </TouchableOpacity>
      ),
      createdBy: infoForm.createdBy,
      notionName:
        statusParty?.find(d => dataModelerForm?.[index]?.userTaskId == d.id)
          ?.notionName ?? 'Form quy trình',
    }));
    setDataForm(newData);
  };

  const convertDataFileToTable = (data: IInfoFileRelative[]) => {
    const newData = data.map((infoFile: IInfoFileRelative, index: number) => ({
      STT: String((currentPage - 1) * pageSize + 1 + index),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {infoFile?.fileName}
        </TextCM>
      ),
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile.size),
      type: infoFile?.fileName?.split('.')?.pop(),
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(false, infoFile),
    }));
    setDataRelative(newData);
  };

  const handleViewFile = async (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.noti'),
        text2: t('TK.viewFile'),
      });

      const formData = new FormData();

      let keyName: string = (data as any).name;
      if (isInfoFile) {
        keyName = (data as IInfoFile).keyName;
      } else {
        keyName = (data as IInfoFileRelative).name;
      }
      let bucketName: string = data.bucketName;
      if (!data.bucketName) {
        const arr = (data as any)?.name?.split('.s3-hfx03.fptcloud.com:443/');
        keyName = arr[1];
        bucketName = arr[0].split('https://')[1];
      }
      formData.append('keyName', keyName);
      formData.append('bucketName', bucketName);
      const dataView = await ReviewTicketServices.viewFile(
        formData,
        accessToken,
      );
      if (dataView?.code == 1) {
        navigation.navigate(NavigationName.ViewFileScreen, {
          url: dataView.message,
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.error'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.error'),
      });
    }
  };

  const handleClearFile = async (idFile: string) => {
    try {
      const data = await ReviewTicketServices.deleteFileRelative(
        idFile,
        accessToken,
      );
      await getListRelativeFile(currentPage);
      Toast.show({
        type: 'success',
        text1: t('CM.noti'),
        text2: t('TK.deleteFileSuccess'),
      });
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('TK.deleteFileErr'),
      });
    }
  };

  const handleUploadFile = async () => {
    try {
      const file = await CommonUtils.handleGetFile();
      if (!file) return;

      const formData = new FormData();
      formData.append('uploadFile', file);
      formData.append(
        'attachmentJson',
        JSON.stringify({
          ticketId: dataTicketRedux?.id,
        }),
      );
      const data = await ReviewTicketServices.uploadFileRelative(
        formData,
        accessToken,
      );
      if (data) {
        await getListRelativeFile(currentPage);
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('TK.uploadFileSuccess'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('TK.uploadFileErr'),
        });
      }
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: 'Thông báo',
        text2: t('TK.uploadFileErr'),
      });
    }
  };

  const handleDownload = () => {
    onDownloadStart();
  };

  const renderActionInfoFile = (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    return (
      <View style={styles.ctnAction}>
        <TouchableOpacity
          onPress={() => handleViewFile(isInfoFile, data)}
          hitSlop={SizeDP(15)}>
          <IconView width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        <TouchableOpacity onPress={handleDownload} hitSlop={SizeDP(15)}>
          <IconDownload width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        {!isInfoFile && (
          <TouchableOpacity
            onPress={() => handleClearFile(data?.id?.toString())}
            hitSlop={SizeDP(15)}>
            <IconTrash width={SizeDP(16)} height={SizeDP(16)} />
          </TouchableOpacity>
        )}
      </View>
    );
  };

  return (
    <SafeAreaView style={{flex: 1}} edges={[]}>
      <ScrollView showsVerticalScrollIndicator={false}>
        <View style={styles.ctnForm}>
          <View
            style={{
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'space-between',
              marginRight: SizeDP(12),
            }}>
            <TextCM style={styles.txtTitle}>{t('TK.docRelative')}</TextCM>
            {(dataTicketRedux.status === Status.processing ||
              dataTicketRedux.ticketStatus === Status.processing) && (
              <TouchableOpacity onPress={handleUploadFile} hitSlop={SizeDP(15)}>
                <IconUpload width={SizeDP(16)} height={SizeDP(16)} />
              </TouchableOpacity>
            )}
          </View>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataRelative}
          />
          {totalItems > 0 && (
            <PagingCM
              collectionSize={totalItems}
              currentPage={currentPage}
              pageSize={pageSize}
              onPageChange={currentPage => setCurrentPage(currentPage)}
            />
          )}
        </View>
        <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateInfo')}</TextCM>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataInfoFile}
          />
        </View>
        <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateForm')}</TextCM>
          <TableCM
            stickyLeft={false}
            tableHeaders={['STT', 'keyName', 'createdBy', 'notionName']}
            tableData={dataForm}
          />
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

//make this component available to the app
export default Document;
