//import liraries
import React, {useCallback, useEffect, useState} from 'react';
import {
  Dimensions,
  FlatList,
  Image,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import {IconDefault, IconEmpty} from '../../../assets/images';
import {IScreen, SizeDP, TYPE_SCREEN} from '../../../constants/Size';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {IServices} from '../../../model/interface/service.interface';
import {isNull} from 'lodash';
import {Color, NavigationName, String} from '../../../constants';
import HeaderCM from '../../components/Header/HeaderCM';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {Icon, Spinner, TopNavigationAction} from '@ui-kitten/components';
import {commonActions} from '../../../redux/slices';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
// create a component
const Home = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const dispatch = useAppDispatch();
  const [listServices, setListServices] = useState<IServices[]>([]);
  const [page, setPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [refreshing, setRefreshing] = useState(false);
  const [screenDevices, setScreenDevices] = useState<IScreen>({
    width: Dimensions.get('screen').width,
    height: Dimensions.get('screen').height,
  });

  const {accessToken} = useAppSelector(state => state.common);
  const ListServices = useTicketServices();
  useEffect(() => {
    const sub = Dimensions.addEventListener('change', ({screen}) => {
      setScreenDevices({
        width: screen.width,
        height: screen.height,
      });
    });

    return () => sub.remove();
  }, []);
  useFocusEffect(
    useCallback(() => {
      (async () => {
        await getListServices(0);
      })();
    }, []),
  );

  const getListServices = async (page: number) => {
    try {
      try {
        // const datax = await ListServices.login();
        // dispatch(commonActions.setAccessToken(datax.access_token));
        const params = {
          sort: 'displayOrder,asc',
          page: page,
          size: 10,
          sort: 'createdDate,desc',
        };
        const body = {
          isAdmin: false,
          status: ['display'],
          textSearch: '',
        };
        const data = await ListServices.getListServives(
          body,
          params,
          accessToken,
        );
        if (page === 0) {
          setListServices(data.content);
        } else {
          setListServices([...listServices, ...data.content]);
        }
        setTotalPages(data?.page?.totalPage);
      } catch (error) {
        console.log('error', error);
      } finally {
        setRefreshing(false);
      }
    } catch (error) {}
  };

  const handleServiceDetail = (item: IServices) => {
    navigation.navigate(NavigationName.ServiceDetail, item);
  };

  const renderContentRight = () => {
    return (
      <>
        <TopNavigationAction
          icon={<Icon fill={'#ffffff'} name="search" />}
          onPress={() => {
            navigation.navigate(NavigationName.SearchHomeScreen, {});
          }}
        />
      </>
    );
  };

  const renderListServiceItem = ({item}: {item: IServices}) => {
    return (
      <TouchableOpacity
        onPress={() => handleServiceDetail(item)}
        style={[styles.ctnItemList, {flex: 1}]}>
        <View
          style={[
            styles.ctnImage,
            {
              backgroundColor: !isNull(item.color)
                ? item.color
                : Color.BgPrimary,
            },
          ]}>
          {isNull(item.iconBase64) ? (
            <IconDefault width={SizeDP(28)} height={SizeDP(28)} />
          ) : (
            <Image
              source={{
                uri: `${String.PREFIX_IMG_BASE64}${item.iconBase64}`,
              }}
            />
          )}
        </View>
        <View style={{flex: 1, rowGap: SizeDP(8)}}>
          <TextCM style={styles.textTitleItem}>{item.serviceName}</TextCM>
          <TextCM style={styles.textDes}>{item.description}</TextCM>
        </View>
      </TouchableOpacity>
    );
  };

  const onRefresh = () => {
    setRefreshing(true);
    setPage(0);
    getListServices(0);
  };

  const fetchNextPage = () => {
    if (page < totalPages) {
      getListServices(page + 1);
      setPage(page + 1);
    }
  };

  return (
    <>
      <HeaderCM title="eRequest" renderContentRight={renderContentRight} />
      <View style={styles.ctnMain}>
        <TextCM style={styles.textTitle}>{t('HM.title')}</TextCM>
        <>
          {screenDevices?.width > TYPE_SCREEN.tablet_h ? (
            <FlatList
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              data={listServices}
              renderItem={renderListServiceItem}
              keyExtractor={item => '_' + item.id}
              key={'_'}
              numColumns={2}
              columnWrapperStyle={{columnGap: 20}}
              onEndReachedThreshold={0.5}
              onEndReached={fetchNextPage}
              refreshing={refreshing}
              onRefresh={onRefresh}
              ListEmptyComponent={
                <View style={styles.viewListServices}>
                  <IconEmpty width={SizeDP(218)} height={SizeDP(149)} />
                  <TextCM style={styles.textH1}>{t('HM.titleEmpty')}</TextCM>
                  <TextCM>{t('HM.description')}</TextCM>
                </View>
              }
            />
          ) : (
            <FlatList
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              data={listServices}
              renderItem={renderListServiceItem}
              keyExtractor={item => '#' + item.id}
              key={'#'}
              numColumns={1}
              onEndReachedThreshold={0.5}
              onEndReached={fetchNextPage}
              refreshing={refreshing}
              onRefresh={onRefresh}
              ListEmptyComponent={
                <View style={styles.viewListServices}>
                  <IconEmpty width={SizeDP(218)} height={SizeDP(149)} />
                  <TextCM style={styles.textH1}>{t('HM.titleEmpty')}</TextCM>
                  <TextCM>{t('HM.description')}</TextCM>
                </View>
              }
            />
          )}
        </>
      </View>
    </>
  );
};

//make this component available to the app
export default Home;

